<template>
  <div class="mdl-cell--4-col-phone">
     <a
      class="custom-button"
      @click="toggleEditView"
      v-show="showEditView"
    >
     Back
    </a>
    <router-link
      tag="button"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--right custom-button--primary"
      :to="{ name: 'clients.create' }"
    >
      <i class="material-icons">add</i> Add
    </router-link>
    <div class="mdl-layout-spacer"></div>

    <data-table
      :columns="columns"
      :data="clients.data"
      :actionMethod="fetchClients"
      :count="clients.count"
      @rowSelected="clientSelected"
      v-show="!showEditView"
    ></data-table>
    <client-update-form
      :client="client"
      v-show="showEditView"
    >
    </client-update-form>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DataTable } from 'SHARED/data_table'
import { ClientUpdateForm } from '.'
export default {
  name: 'clients-list-view',
  components: {
    DataTable,
    ClientUpdateForm
  },
  computed: {
    ...mapGetters({
      clients: 'getClients'
    })
  },
  mounted () {
    window.componentHandler.upgradeAllRegistered()
  },
  methods: {
    ...mapActions([
      'fetchClients'
    ]),
    clientSelected (client) {
      this.client = client
      this.showEditView = true
    },
    toggleEditView () {
      this.showEditView = !this.showEditView
    }
  },
  data () {
    return {
      showEditView: false,
      client: {},
      columns: [
        {
          field: 'name',
          label: 'Name',
          type: String
        },
        {
          field: 'gst_number',
          label: 'GST Number',
          type: String
        },
        {
          field: 'email',
          label: 'Email',
          type: String
        },
        {
          field: 'is_active',
          label: 'Active',
          type: Boolean
        }
      ]
    }
  }
}
</script>



